import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import { WithdrawCheckApi, WithdrawSaveApi, WithdrawSubmitApi } from 'utils/Api';
const { banks } = require('utils/bank');

const Withdraw = () => {
  const navigate = useNavigate();
  const { t } = useTranslation();
  const [withdrawInfo, setWithdrawInfo] = useState({
		_id: '',
    name: '',
    bankAccount: '',
    bank: {
      code: '',
      value: '',
    },
  });
	const [withdrawBlance, setWithdrawBlance] = useState('')

  useEffect(() => {
    WithdrawCheckApi()
      .then((res) => {
				if(res.data){
					setWithdrawInfo(res.data)
				}
			})
      .catch(() => {
        navigate('/wallet');
      });
  }, []);

	const handleInput = (e, type) => {
		if(type === 'info'){
			setWithdrawInfo({
				...withdrawInfo,
				[e.target.name]: e.target.value
			})
		}else if(type === 'blance'){
			setWithdrawBlance(e.target.value)
		}
	}
  const handleSelect = (e) => {
    setWithdrawInfo({
      ...withdrawInfo,
      bank: {
        code: e.target.title,
        value: e.target.text,
      },
    });
  };

	const handleSave = (e) => {
    e.preventDefault();
		WithdrawSaveApi(withdrawInfo).then((res) => {
			setWithdrawInfo(res.data)		
		})
	}

	const handleSubmit = (e) => {
		e.preventDefault()
		if(withdrawBlance === ''){
			toast.warning(t('please_input_blance'))
			return
		}
		if(withdrawInfo._id === ''){
			toast.warning(t('please_input_info'))
		}else{
			WithdrawSubmitApi({amount: withdrawBlance, bankInfo: withdrawInfo}).then((res) => {
				navigate('/wallet')
		})
		}
	}

  return (
    <div className="flex flex-col justify-center items-center h-full ">
      <div className="h-2/4">
        <div>
          <input
						disabled
            className="hidden h-12 flex-auto outline-none px-2 rounded-lg border-2"
						name='_id'
						value={withdrawInfo._id}
          />
        </div>
        <div>
          <label className="font-bold text-teal-600">{t('real_name')}:</label>
          <input
            className="h-12 flex-auto outline-none px-2 rounded-lg border-2"
						name='name'
						value={withdrawInfo.name}
						onChange={(e) => handleInput(e, 'info')}
          />
        </div>
        <div className="mt-4">
          <label className="font-bold text-teal-600">
            {t('bank_account')}:
          </label>
          <input
						name='bankAccount'
            className="h-12 flex-auto outline-none px-2 rounded-lg border-2"
						value={withdrawInfo.bankAccount}
						onChange={(e) => handleInput(e, 'info')}
          />
        </div>
        <div className="mt-4 flex h-24">
          <label className="font-bold text-teal-600">{t('choice_bank')}:</label>
          <div className="dropdown relative ">
            <button
              className="
								dropdown-toggle
								w-48
    		        px-2 py-3 bg-orange-500 text-white font-bold text-sm leading-snug rounded shadow-md transition duration-150 ease-in-out flex
								justify-between
        				items-center
        				"
              type="button"
              id="dropdownMenuLargeButton"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {withdrawInfo.bank.value === ''
                ? t('please_choice_bank')
                : withdrawInfo.bank.value}

              <svg
                aria-hidden="true"
                focusable="false"
                data-prefix="fas"
                data-icon="caret-down"
                className="w-2 ml-2"
                role="img"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 320 512"
              >
                <path
                  fill="currentColor"
                  d="M31.3 192h257.3c17.8 0 26.7 21.5 14.1 34.1L174.1 354.8c-7.8 7.8-20.5 7.8-28.3 0L17.2 226.1C4.6 213.5 13.5 192 31.3 192z"
                ></path>
              </svg>
            </button>
            <ul
              className="
        				dropdown-menu
								w-full
			        	absolute
    			    	hidden
        				bg-orange-500
        				z-50
			        	float-left
    			    	py-2
        				list-none
			        	text-left
    			    	rounded-lg
        				shadow-lg
			        	mt-1
    			    	m-0
        				bg-clip-padding
        				border-none"
              aria-labelledby="dropdownMenuLargeButton"
            >
              {banks.map((bank, index) => (
                <li key={index}>
                  <a
                    title={bank.code}
                    onClick={handleSelect}
                    className="
            	dropdown-item
            	text-xs
            	py-2
            	px-4
            	font-bold
							text-white
            	block
            	bg-transparent
            	hover:bg-orange-400
										border-solid
										border-b-orange-400
							border-b-2
										"

                  >
                    {bank.value}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        </div>
					<div >
        <button className="mx-24 rounded-lg text-white font-bold bg-teal-600 h-12 w-24" onClick={handleSave}>
          {t('save_info')}
        </button>
					</div>
      </div>
      <div className=" flex flex-col items-center">
        <div className="flex h-24 justify-center items-center gap-2">
          <label className="font-bold text-teal-600">提现金额:</label>
          <input
            type="number"
						value={withdrawBlance}
            className="h-12 flex-auto outline-none px-2 rounded-lg border-2"
						onChange={(e) => handleInput(e, 'blance')}
          ></input>
        </div>
        <button className="rounded-lg text-white font-bold bg-teal-600 h-12 w-24" onClick={handleSubmit}>
          {t('submit')}
        </button>
      </div>
    </div>
  );
};

export default Withdraw;
